"use client";
import { useEffect, useState } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Eye, MousePointerClick, Mail } from "lucide-react";
import { Button } from "@/components/ui/button";
import { useRouter } from "next/navigation";

interface EmailLog {
  _id: string;
  to: string;
  subject: string;
  opened: boolean;
  openedAt?: string;
  clickCount: number;
}

export default function DashboardPage() {
  const router = useRouter();
  const [emails, setEmails] = useState<EmailLog[]>([]);
  const [loading, setLoading] = useState(true);

  const ITEMS_PER_PAGE = 10;
  const [currentPage, setCurrentPage] = useState(1);

  const totalPages = Math.ceil(emails.length / ITEMS_PER_PAGE);
  const paginatedEmails = emails.slice(
    (currentPage - 1) * ITEMS_PER_PAGE,
    currentPage * ITEMS_PER_PAGE
  );

  useEffect(() => {
    fetch("/api/emails", {
      headers: {
        authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    })
      .then((res) => res.json())
      .then((data) => setEmails(data))
      .finally(() => setLoading(false));
  }, []);

  if (loading) {
    return (
      <div className="flex h-[60vh] items-center justify-center text-xl font-semibold text-muted-foreground">
        Loading dashboard...
      </div>
    );
  }

  return (
    <main className="p-4 md:p-10 max-w-7xl mx-auto space-y-8">
      {/* Header */}
      <header className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <h1 className="text-3xl font-bold tracking-tight">
          Email Tracking Dashboard
        </h1>

        <Button
          className="rounded-xl bg-gradient-to-r from-blue-600 to-indigo-600 hover:opacity-90"
          onClick={() => router.push("/send")}
        >
          + Send Email
        </Button>
      </header>

      {/* Stat Cards */}
      <section className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
        <StatCard
          title="Total Emails"
          value={emails.length}
          icon={<Mail />}
          gradient="from-blue-500 to-indigo-500"
        />

        <StatCard
          title="Opened"
          value={emails.filter((e) => e.opened).length}
          icon={<Eye />}
          gradient="from-green-500 to-emerald-500"
        />

        <StatCard
          title="Total Clicks"
          value={emails.reduce((a, b) => a + (b.clickCount || 0), 0)}
          icon={<MousePointerClick />}
          gradient="from-pink-500 to-rose-500"
        />
      </section>

      {/* Table */}
      <section className="rounded-2xl border shadow-sm overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead className="bg-muted/60">
              <tr>
                <th className="p-4 text-left">Recipient</th>
                <th className="p-4 text-left">Subject</th>
                <th className="p-4 text-center">Status</th>
                <th className="p-4 text-center">Opened At</th>
                <th className="p-4 text-center">Clicks</th>
              </tr>
            </thead>

            <tbody>
              {paginatedEmails.map((email) => (
                <tr
                  key={email._id}
                  className="border-t hover:bg-muted/40 transition"
                >
                  <td className="p-4 font-medium">{email.to}</td>

                  <td className="p-4">{email.subject}</td>

                  {/* Status Badge */}
                  <td className="p-4 text-center">
                    <span
                      className={`px-3 py-1 rounded-full text-xs font-semibold ${
                        email.opened
                          ? "bg-green-100 text-green-700"
                          : "bg-gray-200 text-gray-600"
                      }`}
                    >
                      {email.opened ? "Opened" : "Pending"}
                    </span>
                  </td>

                  <td className="p-4 text-center text-muted-foreground">
                    {email.openedAt
                      ? new Date(email.openedAt).toLocaleString()
                      : "â€”"}
                  </td>

                  <td className="p-4 text-center font-semibold">
                    {email.clickCount}
                  </td>
                </tr>
              ))}

              {paginatedEmails.length === 0 && (
                <tr>
                  <td colSpan={5} className="text-center p-10">
                    <p className="text-muted-foreground text-lg">
                      No emails found
                    </p>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </section>

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="flex flex-col sm:flex-row items-center justify-between gap-4">
          <p className="text-sm text-muted-foreground">
            Page {currentPage} of {totalPages}
          </p>

          <div className="flex gap-2">
            <Button
              variant="outline"
              size="sm"
              disabled={currentPage === 1}
              onClick={() => setCurrentPage((p) => p - 1)}
            >
              Previous
            </Button>

            <Button
              variant="outline"
              size="sm"
              disabled={currentPage === totalPages}
              onClick={() => setCurrentPage((p) => p + 1)}
            >
              Next
            </Button>
          </div>
        </div>
      )}
    </main>
  );
}

/* ---------------- Stat Card ---------------- */

function StatCard({
  title,
  value,
  icon,
  gradient,
}: {
  title: string;
  value: number;
  icon: React.ReactNode;
  gradient: string;
}) {
  return (
    <Card className="rounded-2xl shadow-sm hover:shadow-md transition">
      <CardContent className="p-6 flex items-center gap-4">
        <div
          className={`p-3 rounded-xl text-white bg-gradient-to-br ${gradient}`}
        >
          {icon}
        </div>

        <div>
          <p className="text-sm text-muted-foreground">{title}</p>
          <p className="text-2xl font-bold">{value}</p>
        </div>
      </CardContent>
    </Card>
  );
}
